import React, { Component } from 'react';
import {Layout, Header, Content, Navigation, Drawer, Textfield, Button} from "react-mdl";
import Router from "react-router-dom/HashRouter";
import Link from "react-router-dom/Link"
import Match from "react-router-dom/Match"
import Redirect from "react-router/Redirect"
import axios from 'axios';
import mainscreen from './mainscreen';

class login extends Component {
	constructor(props){
  super(props);
  this.state={
  username:'',
  password:''
  }
 }
 handleClick(event){
 var apiBaseUrl = "https://172.17.1.25:9090/oauth";
 var self = this;
 var config = {
  headers: {'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'POST, GET, OPTIONS',
          'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept',
          'Authorization': 'Bearer MY-API-KEY'
        }
}
 var payload={
 "usuario":this.state.username,
 "password":this.state.password
 }
 axios.post(apiBaseUrl, payload, config)
 .then(function (response) {
 console.log(response);
 if(response.data.code === 200){
 console.log("Login successfull");
 var main=[];
 main.push(<mainscreen appContext={self.props.appContext}/>)
 self.props.appContext.setState({loginPage:[],mainscreen:mainscreen})
 }
 else if(response.data.code === 204){
 console.log("Username password do not match");
 alert("username password do not match")
 }
 else{
 console.log("Username does not exists");
 alert("Username does not exist");
 }
 })
 .catch(function (error) {
 console.log(error);
 });
 }
  render() {
    return (
      <div className="App">
          <Router>
      <Layout fixedHeader>
      <Header title="FitBank">          </Header>
                 <Content>
                 <br/><br/>
               <Textfield
             label="Username"
             floatingLabel
              style={{width: '200px'}}
             onChange = {(event,newValue) => this.setState({username:newValue})}
             />
           <br/>
             <Textfield
               type="password"
               label="Password"
               floatingLabel
                style={{width: '200px'}}
               onChange = {(event,newValue) => this.setState({password:newValue})}
               />
             <br/>
             <Button raised accent ripple primary={true} style={style} onClick={(event) => this.handleClick(event)}>Login</Button>
             
               
                </Content>
                </Layout>
                </Router>
      </div>
    );
  }
}
const style = {
 margin: 15,
};
export default login;



